.welcome {
  height: 720px;
  position: relative;
  overflow: hidden;
  margin-top: -126px;

  &::before {
    content: url(/assets/src/img/welcome/decor.png);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
}

.welcome__body {
  display: flex;
  gap: 104px;
}

.welcome__content {
  max-width: 510px;
  padding-top: 182px;
  z-index: 1;
}

.welcome__title {
  color: #1B1C57;
  font-size: 40px;
  font-weight: 600;
  text-transform: capitalize;
  margin-bottom: 24px;

  span {
    color: #fff;
    text-shadow: 1px 0 0 #1B1C57,
                  0 1px 0 #1B1C57,
                  -1px 0 0 #1B1C57,
                  0 -1px 0 #1B1C57;
  }
}

.welcome__text {
  font-size: 16px;
  font-weight: 400;
}

.welcome__text--1 {
  color: #626687;
  line-height: lh(16,28);
  margin-bottom: 32px;
  max-width: 510px;
}

.welcome__text--2 {
  color: #888B97;
  line-height: lh(16,32);
}

.welcome__form {
  border: 1px solid #E0E3EB;
  margin-bottom: 32px;

  & > svg {
    fill: #F59E0B;
  }

  button {

    svg {
      height: 24px;
      width: 24px;
      fill: #fff;
      transform: rotate(180deg);
    }
  }
}

.welcome__banners {

  img {
    height: 64px;
    width: 83px;
    margin-right: 32px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.welcome__img {
  flex-grow: 1;
}

.welcome__imgBody {
  position: absolute;
  inset: 0 0 0 50%;
  overflow: hidden;
  border-bottom-left-radius: 80px;
  min-width: 720px;
}

.welcome__imgBg {

  img {
    object-position: center top;
  }
}

.welcome__imgInfo {
  display: flex;
  align-items: center;
  gap: 16px;
  inset: auto auto 48px 48px;
  position: absolute;
  width: 860px;
}

.welcome__imgInfoItem {
  display: flex;
  align-items: center;
  border-radius: 32px;
  background: #fff;
  padding: 24px;
  box-shadow: 0 11px 48px 0 rgba(51, 51, 51, 0.06);
}

.welcome__imgInfoItemImgs {
  display: flex;
  align-items: center;
  margin-right: 16px;
}

.welcome__imgInfoItemImg {
  height: 56px;
  width: 56px;
  overflow: hidden;
  border: 3px solid #fff;
  border-radius: 50%;
  box-shadow: 0 9px 32px 0 rgba(89, 92, 219, 0.1);

  & + .welcome__imgInfoItemImg {
    margin-left: -28px;
  }
}

.welcome__imgInfoItemContent {
  flex-shrink: 0;
}

.welcome__imgInfoItemTitle {
  color: #1B1C57;
  font-size: 16px;
  font-weight: 600;
  line-height: lh(16,24);
}

.welcome__imgInfoItemText {
  color: #68799F;
  font-size: 12px;
  font-weight: 400;
  line-height: lh(12,20);
}


@mixin welcome-1200 {

  .welcome {

    &::before {
      display: none;
    }
  }

  .welcome__body {
    flex-direction: column;
  }

  .welcome__content {
    padding-top: 115px;
    max-width: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .welcome__text--1 {
    color: #1B1C57;
    max-width: none;
  }

  .welcome__form {
    align-self: center;
    min-width: 468px;
  }

  .welcome__partners {
    background-color: #fff;
    padding: 20px 20px 0;
    border-radius: 32px;
    align-self: center;
  }

  .welcome__imgBody{
    left: 0;
    border-bottom-left-radius: 0;

    &::before {
      content: '';
      display: block;
      position: absolute;
      inset: 0;
      background-color: rgba(0,0,0,0.05);
    }
  }

  .welcome__imgInfo {
    inset: auto 20px 20px;
    width: auto;
    justify-content: center;
  }

}


@mixin welcome-992 {

  .welcome__title {
    font-size: 35px;
  }

  .welcome__imgInfoItem {
    border-radius: 20px;
    padding: 14px;
  }

  .welcome__imgInfoItemImgs {
    margin-right: 5px;
  }

  .welcome__imgInfoItemImg {
    height: 45px;
    width: 45px;
  }

}


@mixin welcome-768 {

  .welcome {
    height: auto;
    margin-top: -86px;
  }

  .welcome__body {
    gap: 0;
  }

  .welcome__content {
    margin-bottom: 40px;
  }

  .welcome__imgBody {
    position: relative;
    min-width: auto;

    &::before {
      display: none;
    }
  }

  .welcome__imgBg {
    display: none;
  }

  .welcome__imgInfo {
    position: relative;
    flex-wrap: wrap;
    inset: auto;
  }

  .welcome__imgInfoItem {
    border: 1px solid rgba(0,0,0,0.1);
    box-shadow: none;
  }

}


@mixin welcome-576 {

  .welcome__content {
    padding-top: 90px;
    margin-bottom: 10px;
  }

  .welcome__title {
    font-size: calc( var(--indDesc) * 2.51 );
  }

  .welcome__text--1 {
    margin-bottom: 20px;
  }

  .welcome__form {
    min-width: auto;
    margin-bottom: 0;
    padding-left: 10px;
    width: 100%;

    input {
      width: 100%;
    }

    svg {
      display: none;
    }
  }

}