.subscribe {
  position: relative;
  height: 312px;
  overflow: hidden;
}

.subscribe__body {
  border-radius: 32px;
  background: url(/assets/src/img/subscribe/bg.png) center/cover no-repeat;
  padding: 65px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1200px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.subscribe__title {
  line-height: lh(32,48);
  margin-bottom: 32px;
  max-width: 425px;
  text-align: center;
}

.subscribe__form {
  width: 100%;
  max-width: 500px;

  svg {
    fill: #3B82F6;
  }
}

.subscribe__decor {
  border: 2px solid #fff;
  position: absolute;
  overflow: hidden;

  &:nth-child(1) {
    top: 38px;
    left: 44px;
    height: 81px;
    width: 81px;
  }

  &:nth-child(2) {
    bottom: 80px;
    left: 195px;
    height: 54px;
    width: 54px;
  }

  &:nth-child(3) {
    top: 44px;
    right: 200px;
    height: 70px;
    width: 70px;
  }

  &:nth-child(4) {
    right: 73px;
    bottom: 60px;
    height: 55px;
    width: 55px;
  }

  &:nth-child(5) {
    left: 67px;
    bottom: 103px;
  }

  &:nth-child(6) {
    left: 204px;
    top: 71px;
  }

  &:nth-child(7) {
    right: 200px;
    bottom: 133px;
  }

  &:nth-child(8) {
    top: 107px;
    right: 82px;
  }
}

.subscribe__decor--object {
  border-radius: 24px;
}

.subscribe__decor--person {
  border-radius: 50%;
  box-shadow: 0 9px 32px 0 rgba(89, 92, 219, 0.1);
  width: 36px;
  height: 36px;
}


@mixin subscribe-576 {

  .subscribe {
    height: auto;
  }

  .subscribe__body {
    position: relative;
    width: auto;
    padding: 40px 15px;
  }

  .subscribe__decor {
    display: none;
  }

  .subscribe__form {
    padding-left: 10px;
    width: 100%;

    input {
      width: 100%;
    }

    svg {
      display: none;
    }

    button {

      span {

        &:last-child {
          display: none;
        }
      }
    }
  }

}