.header {
  z-index: var(--ziHeader);

  nav {

    & > ul {

      & > li {
        position: relative;

        &.header__submenuWrapper {

          & > a {

            svg {
              display: block;
            }
          }
        }

        & > a {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 8px 16px;
          border-radius: 32px;
          border: 1px solid rgba(255, 255, 255, 0.3);
          background-color: rgba(255, 255, 255, 0.1);

          & > svg {
            display: none;
            fill: #F0F3FD;
            height: 18px;
            width: 18px;
            margin-left: 10px;
          }
        }

        & > ul {
          border: 1px solid rgba(255, 255, 255, 0.3);
          background-color: rgba(255, 255, 255, 0.1);
          border-radius: 15px;
          padding: 10px 15px;

          li {
            margin: 0 0 15px;

            &:last-child {
              margin-bottom: 0;
            }
          }
        }

      }
    }

    a {
      font-family: Lexend, sans-serif;
      color: #F0F3FD;
      font-size: 14px;
      font-weight: 500;
      text-transform: capitalize;
    }
  }
}

.header__body {
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 56px;
}

.header__menu {
  flex-grow: 1;

  & > ul {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    column-gap: 24px;

    & > li {

      & > ul {
        position: absolute;
        inset: 46px auto auto 0;
        min-width: 100%;
        white-space: nowrap;
      }
    }
  }
}

.header__btn {
  background-color: #D1FAE5;
  color: #047857;
}

.header__burgerContainer {

  nav {
    max-width: 250px;

    & > ul {

      & > li {

        & > ul {
          margin: 20px 0;
        }
      }
    }
  }
}


@mixin header-992 {

  .header__body {
    column-gap: 0;
  }

  .header__menu {
    display: none;
  }

}


@mixin header-576 {

  .header__body {
    padding: 20px 0;
  }

  .header__logo {
    height: 31px;
    width: 104px;
  }

}
