.partner {
  position: relative;

  &::before {
    content: url(/assets/src/img/partner/decor.png);
    display: block;
    position: absolute;
    top: -170px;
    left: 0;
    z-index: -1;
  }
}

.partner__titleWrapper {
  margin-bottom: 24px;
  text-align: center;
}

.partner__btn {
  background-color: #10B981;
  display: flex;
  margin: 0 auto 42px;
}

.partner__content {
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
  gap: 68px;
}

.partner__manyRow {
  max-width: 572px;

  .partner__item {
    margin-bottom: 44px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.partner__oneRow {
  max-width: 560px;

  .partner__item {
    flex-direction: column;
    align-items: flex-start;
    row-gap: 0;
  }

  .partner__itemImg {
    width: 100%;
    height: 280px;
    margin-bottom: 24px;
  }

  .partner__itemContent {
    max-width: none;
  }
}

.partner__item {
  display: flex;
  align-items: center;
  gap: 16px 32px;
}

.partner__itemImg {
  border-radius: 16px;
  overflow: hidden;
  width: 200px;
  height: 144px;
  flex-shrink: 0;
}

.partner__itemContent {
  overflow: hidden;
}

.partner__itemPerson {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.partner__itemPersonImg {
  border-radius: 50%;
  height: 32px;
  width: 32px;
  overflow: hidden;
  margin-right: 16px;
}

.partner__itemPersonName {
  color: #3C4563;
  line-height: lh(14,22);
}

.partner__itemTitle {
  color: #1B1C57;
  font-size: 18px;
  font-weight: 400;
  line-height: lh(18,32);
  text-transform: capitalize;
  margin-bottom: 16px;
}

.partner__itemText {
  color: rgba(98,102,135,0.75);
  margin-bottom: 16px;
}

.partner__itemDate {
  display: flex;
  align-items: center;

  svg {
    fill: #888B97;
    height: 24px;
    width: 24px;
    margin-right: 12px;
  }
}


@mixin partner-1200 {

  .partner {

    &:before {
      display: none;
    }
  }
}


@mixin partner-768 {

  .partner__content {
    gap: 40px 0;
  }

  .partner__item {
    gap: 16px;
  }

  .partner__itemTitle {
    font-size: 16px;
  }

}


@mixin partner-576 {

  .partner__btn {
    margin-bottom: 30px;
  }

  .partner__manyRow {
    max-width: none;
    width: 100%;

    .partner__item {
      margin-bottom: calc( var(--indDesc) * 3.5 );
      flex-direction: column;
      text-align: center;
    }
  }

  .partner__oneRow {
    max-width: none;

    .partner__itemImg {
      height: calc( var(--indDesc) * 20.54 );
      margin-bottom: calc( var(--indDesc) * 1.76 );
    }
  }

  .partner__content {
    justify-content: flex-start;
    row-gap: calc( var(--indDesc) * 2.93 );
  }

  .partner__itemContent {
    max-width: 270px;
  }

  .partner__itemImg {
    height: 145px;
    width: 200px;
  }

  .partner__itemPerson {
    justify-content: center;
  }

  .partner__itemDate {
    justify-content: center;
  }

}