.recommendation {
  margin-bottom: 120px;
  padding-bottom: 552px;
  position: relative;
  overflow: hidden;
  font-size: 14px;

  .slick-slide {
    padding: 0 20px;
  }
}

.recommendation__top {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 40px;
  padding-left: 40px;
}

.recommendation__filterBtns {
  display: flex;
}

.recommendation__filterBtn {
  display: flex;
  align-self: center;
  color: #888B97;
  font-size: 18px;
  font-weight: 500;
  line-height: lh(18,28);
  margin-right: 32px;
  border: 1px solid #E0E3EB;
  padding: 12px 24px;
  transition: transform 0.3s, background-color 0.3s, color 0.3s, border-color 0.3s;

  &.active {
    background-color: #D1FAE5;
    color: #10B981;
    border-color: transparent;

    svg {
      fill: #10B981;
    }
  }

  &:last-child {
    margin-right: 0;
  }

  svg {
    fill: #888B97;
    margin-right: 8px;
    transition: fill 0.3s;
  }
}

.recommendation__sliderBtns {
  display: flex;
  align-items: center;
}

.recommendation__sliderBtn {
  background-color: #E0E3EB;
  padding: 12px 16px;
  transition: transform 0.3s, background-color 0.3s;

  &:hover,
  &:focus-visible {
    background-color: #10B981;

    svg {
      fill: #fff;
    }
  }

  svg {
    fill: #3C4563;
    transition: fill 0.3s;
  }
}

.recommendation__sliderBtn--prev {
  margin-right: 16px;
}

.recommendation__sliderBtn--next {

  svg {
    transform: rotate(180deg);
  }
}

.recommendation__sliderItem {
  width: 340px;
  font-weight: 400;
}

.recommendation__slider {
  margin: 0 -20px;
  padding-left: 40px;
  position: absolute;
  width: 1560px;
}

.recommendation__sliderItemImg {
  position: relative;
  border-radius: 24px;
  height: 382px;
  margin-bottom: 24px;
  overflow: hidden;
  flex-grow: 1;
}

.recommendation__sliderItemNote {
  position: absolute;
  inset: auto auto 16px 16px;
  padding: 8px 16px;
  font-size: 1em;
  text-transform: capitalize;
  border-radius: 32px;
  display: flex;
  align-items: center;

  svg {
    margin-right: 10px;
    height: 16px;
    width: 16px;
  }

  &--fire {
    color: #EF4444;
    background-color: #FEE2E2;

    svg {
      fill: #EF4444;
    }
  }

  &--house {
    color: #1D4ED8;
    background-color: #DBEAFE;

    svg {
      fill: #1D4ED8;
    }
  }

  &--wallet {
    color: #047857;
    background-color: #D1FAE5;

    svg {
      fill: #047857;
    }
  }
}

.recommendation__sliderItemTitle {
  color: #0E1735;
  font-size: 1.71em;
  line-height: lh(24,32);
  margin-bottom: 8px;
}

.recommendation__sliderItemPrice {
  color: #3C4563;
  font-size: 1.43em;
  line-height: lh(20,32);
  margin-bottom: 24px;
}

.recommendation__sliderItemPerson {
  display: flex;
  align-items: center;
}

.recommendation__sliderItemPersonImg {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 16px;
}

.recommendation__sliderItemPersonName {
  color: #0E1735;
  font-size: 1.29em;
  line-height: lh(18,24);
  margin-bottom: 4px;
}

.recommendation__sliderItemPersonCity {
  color: #888B97;
  font-size: 1em;
  line-height: lh(14,22);
}


@mixin recommendation-992 {

  .recommendation__titleWrapper {
    width: 100%;
  }
}


@mixin recommendation-768 {

  .recommendation {
    font-size: 12px;
    padding-bottom: 0;

    .slick-slide {
      padding: 0 15px;
    }
  }

  .recommendation__top {
    gap: 19px;
    padding-left: 20px;
    margin-bottom: 30px;
  }

  .recommendation__filterBtn {
    padding: 7px 17px;
    margin-right: 10px;
    font-size: 16px;

    svg {
      height: 20px;
      width: 20px;
    }
  }

  .recommendation__sliderBtn {
    padding: 7px 10px;
    margin-right: 10px;
  }

  .recommendation__slider {
    margin: 0 -15px;
    padding-left: 20px;
    position: relative;
    width: auto;
  }

  .recommendation__sliderItemImg {
    margin-bottom: 15px;
    height: calc( var(--indDesc) * 23.7 );
  }

  .recommendation__sliderItemPrice {
    margin-bottom: 15px;
  }

}


@mixin recommendation-576 {

  .recommendation__top {
    padding-left: 0;
    margin-bottom: calc( var(--indDesc) * 2.16 );
    gap: calc( var(--indDesc) * 1.3 );
    flex-direction: column;
  }

  .recommendation__filterBtns {
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    width: 100%;
  }

  .recommendation__filterBtn {
    font-size: 14px;
    margin-right: 0;
  }

  .recommendation__sliderBtns {
    align-self: center;
  }

  .recommendation__sliderBtn {

    svg {
      height: calc( var(--indDesc) * 1.97 );
      width: calc( var(--indDesc) * 1.97 );
    }
  }

  .recommendation__slider {
    padding-left: 0;
    max-width: 340px;
    margin: 0 auto;
  }

  .recommendation__sliderItemImg {
    margin-bottom: 15px;
    height: calc(var(--indDesc) * 26);
  }

  .recommendation__sliderItemTitle,
  .recommendation__sliderItemPrice {
    text-align: center;
  }

  .recommendation__sliderItemPerson {
    justify-content: center;
  }

}