.blockForm {
  border-radius: 32px;
  background-color: #fff;
  padding: 4px 4px 4px 24px;
  display: flex;
  align-items: center;
  overflow: hidden;

  & > svg {
    margin-right: 16px;
    height: 24px;
    width: 24px;
  }

  input {
    flex-grow: 1;
    align-self: stretch;
    color: #888B97;
    font-family: Lexend, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: lh(14,22);
    margin-right: 16px;
    border: none;

    @include inputPlaceholder(14, #888B97, 0.5);
  }

  button {
    background-color: #10B981;
    flex-shrink: 0;

    span {
      margin-right: 4px;
    }
  }
}