.blockTitle {

}

.blockTitle--1 {
  color: #1B1C57;
  font-size: 32px;
  font-weight: 600;
  font-family: Lexend, sans-serif;
  text-transform: capitalize;
}


@mixin blockTitle-992 {

  .blockTitle--1 {
    font-size: 28px;
  }

}

@mixin blockTitle-768 {

  .blockTitle--1 {
    font-size: 24px;
  }

}



@mixin blockTitle-576 {

  .blockTitle--1 {
    font-size: 16px;
  }

}