.blockBtn {
  border: none;
  border-radius: 32px;
  font-family: Lexend, sans-serif;
  align-items: center;
  display: inline-flex;
  justify-content: center;
  text-align: center;
  transition: transform 0.3s;

  &:hover,
  &:focus-visible {
    transform: scale(1.1);
  }
}

.blockBtn--1 {
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  line-height: lh(14, 22);
  padding: 12px 16px;
}


@mixin blockBtn-576 {

  .blockBtn {

  }

  .blockBtn--1 {

  }

}