.footer {
  position: relative;
  padding-bottom: 120px;

  &::before {
    content: '';
    background: url(/assets/src/img/footer/decor.png) no-repeat;
    width: 660px;
    height: 507px;
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
  }
}

.footer__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 50px;
}

.footer__logo {
  margin-bottom: 16px;
}

.footer__text {
  margin-bottom: 24px;
  max-width: 320px;
}

.footer__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 30px 88px;
}

.footer__navItemTitle {
  color: #0E1735;
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 24px;
}

.footer__navItemLinks {
  line-height: normal;

  li {
    margin-bottom: 18px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    transition: opacity 0.3s;

    &:hover,
    &:focus-visible {
      opacity: 0.6;
    }
  }
}


@mixin footer-1200 {

  .footer {

    &:before {
      display: none;
    }
  }

}


@mixin footer-992 {

  .footer {
    padding-bottom: 100px;
  }

  .footer__nav {
    column-gap: 40px;
  }

}


@mixin footer-768 {

  .footer {
    padding-bottom: 60px;
  }

}


@mixin footer-576 {

  .footer {
    padding-bottom: calc( var(--indDesc) * 4.24 );
  }

  .footer__navItemTitle {
    font-size: 16px;
  }

}