.sell {
  position: relative;

  &::before {
    content: url(/assets/src/img/sell/decor.png);
    display: block;
    position: absolute;
    top: -280px;
    right: 0;
    z-index: -1;
  }
}

.sell__body {
  display: flex;
  justify-content: space-between;
  gap: 50px;
}

.sell__content {
  padding: 28px 40px;
  max-width: 572px;
}

.sell__titleWrapper {
  margin-bottom: 16px;
}

.sell__contentWrapper {
  max-width: 440px;
}

.sell__text {
  color: rgba(98, 102, 135, 0.75);
  font-size: 16px;
  font-weight: 400;
  line-height: lh(16, 28);
  margin-bottom: 16px;
}

.sell__detail {
  margin-bottom: 32px;
}

.sell__detailTitle {
  color: rgba(27,28,87,0.75);
  font-size: 16px;
  font-weight: 600;
  line-height: lh(16,28);
  margin-bottom: 16px;
}

.sell__detailItems {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 16px 53px;
}

.sell__detailItem {
  display: flex;
  align-items: center;

  svg {
    fill: #888B97;
    margin-right: 16px;
    height: 32px;
    width: 32px;
    flex-shrink: 0;
  }

  span {
    color: #3C4563;
    font-size: 16px;
    font-weight: 400;
    text-transform: capitalize;
    flex-shrink: 0;
  }
}

.sell__separator {
  margin-bottom: 32px;
  height: 2px;
  width: 100%;
  background-color: #F0F3FD;
}

.sell__contact {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 15px;
}

.sell__contactPerson {
  display: flex;
  align-items: center;
}

.sell__contactPersonImg {
  height: 56px;
  width: 56px;
  border-radius: 50%;
  margin-right: 24px;
  overflow: hidden;
}

.sell__contactPersonName {
  color: #0E1735;
  font-size: 18px;
  font-weight: 400;
  line-height: lh(18,24);
  margin-bottom: 4px;
}

.sell__contactPersonProf {
  color: #888B97;
  font-size: 16px;
  font-weight: 400;
  line-height: lh(16,24);
}

.sell__contactBtn {
  background-color: #10B981;

  svg {
    fill: #fff;
    margin-right: 16px;
    height: 24px;
    width: 24px;
  }
}

.sell__media {
  position: relative;
}

.sell__mediaVideo {
  height: 416px;
  width: 488px;
  position: relative;
  border-radius: 4px;
  overflow: hidden;

  button {

    &:hover,
    &:focus-visible {
      opacity: 0.8;
    }

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

}

.sell__mediaVideoBg {
  background-color: rgba(0,0,0,0.2);
  position: absolute;
  inset: 0;
}

.sell__mediaPhotos {
  display: flex;
  align-items: flex-end;
  gap: 10px;
  position: absolute;
  left: -40px;
  top: 291px;
}

.sell__mediaPhoto {
  border-radius: 4px;
  overflow: hidden;
  height: 80px;
  width: 96px;

  &:first-child {
    height: 168px;
    width: 296px;
  }
}


@mixin sell-1200 {

  .sell {

    &:before {
      display: none;
    }
  }

  .sell__body {
    align-items: center;
  }

  .sell__mediaVideo {
    height: calc( var(--indDesc) * 20.93 );
    width: calc( var(--indDesc) * 24.15 );
  }

  .sell__mediaPhotos {
    top: calc( var(--indDesc) * 14.64);
  }

  .sell__mediaPhoto {
    height: calc( var(--indDesc) * 4.02 );
    width: calc( var(--indDesc) * 4.83);

    &:first-child {
      height: calc( var(--indDesc) * 8.45 );
      width: calc( var(--indDesc) * 14.89);
    }
  }

}


@mixin sell-992 {

  .sell__detailItems {
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: 1fr;
    grid-gap: 16px;
  }

  .sell__contactPersonImg {
    margin-right: 15px;
  }

  .sell__contactBtn {

    svg {
      margin-right: 10px;
    }
  }

}


@mixin sell-768 {

  .sell__body {
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
  }

  .sell__detailItems {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
  }

  .sell__media {
    align-self: center;
  }

}


@mixin sell-576 {

  .sell__body {
    align-items: center;
  }

  .sell__content {
    padding: 0;
  }

  .sell__text {
    font-size: 14px;
    text-align: center;
  }

  .sell__detail {
    margin-bottom: 20px;
  }
  
  .sell__detailTitle {
    font-size: 14px;
    text-align: center;
  }

  .sell__detailItems {
    margin: 0 auto;
    width: 80%;
    grid-gap: calc( var(--indDesc) * 1.17 );
  }

  .sell__detailItem {

    svg {
      height: 25px;
      width: 25px;
      margin-right: 10px;
    }

    span {
      font-size: 14px;
    }
  }

  .sell__contact {
    justify-content: center;
  }

  .sell__contactPersonImg {
    height: calc( var(--indDesc) * 4.11);
    width: calc( var(--indDesc) * 4.11);
  }

  .sell__contactPersonName {
    font-size: 16px;
  }

  .sell__contactPersonProf {
    font-size: 14px;
  }

  .sell__media {
    align-self: auto;
  }

  .sell__mediaPhotos {
    position: relative;
    inset: auto;
    margin-top: 30px;
  }

  .sell__mediaPhoto {

    &:first-child {
      width: calc(var(--indDesc) * 12.89);
    }
  }

}