.titleWrapper__subtitle {
  margin: 0 0 12px -40px;
  display: flex;
  align-items: center;

  &::before {
    content: '';
    display: block;
    margin-right: 8px;
    width: 32px;
    height: 1px;
    background-color: #F59E0B;
    border: none;
  }
}

.titleWrapper__subtitle--column {
  flex-direction: column;
  margin: 0 0 12px 0;

  &::before {
    margin: 0 0 8px 0;
  }
}


@mixin titleWrapper-768 {

  .titleWrapper__subtitle {
    margin-left: -20px;
    margin-bottom: 8px;

    &::before {
      margin-right: 5px;
      width: 16px;
    }
  }

}


@mixin titleWrapper-576 {

  .titleWrapper {
    text-align: center;
  }

  .titleWrapper__subtitle {
    display: block;
    margin-left: 0;

    &::before {
      display: none;
    }
  }

}
