.blockMargin {

}

.blockMargin--1 {
  margin-bottom: 120px;
}


@mixin blockMargin-992 {

  .blockMargin--1 {
    margin-bottom: 100px;
  }
}


@mixin blockMargin-768 {

  .blockMargin--1 {
    margin-bottom: 60px;
  }
}


@mixin blockMargin-576 {

  .blockMargin--1 {
    margin-bottom: calc( var(--indDesc) * 4.24 );
  }
}

