.socials {

  ul {
    display: flex;
  }

  li {
    margin-right: 24px;

    &:last-child {
      margin-right: 0;
    }
  }

  a {
    display: inline-block;

    &:hover,
    &:focus {

      svg {
        opacity: 0.8;
      }
    }
  }

  svg {
    height: 32px;
    fill: #3C4563;
    transition: opacity 0.3s;
    width: 32px;
  }
}