.review {
  position: relative;

  .slick-dots {

    li {
      button {
        &::before {
          color: #E0E3EB;
          opacity: 1;
          font-size: 8px;
        }
      }

      &.slick-active {
        button {
          &::before {
            color: #3C4563;
          }
        }
      }
    }
  }
}

.review__body {
  overflow: hidden;
}

.review__titleWrapper {
  text-align: center;
  margin-bottom: 40px;
}

.review__slider {
  width: 2332px;
  left: 50%;
  transform: translateX(-50%);
}

.review__sliderItem {
  padding-bottom: 32px;
}

.review__sliderItemBody {
  max-width: 740px;
  margin: 0 auto;
}

.review__sliderItemImg {
  border-radius: 8px;
  opacity: 0.7;
  width: 740px;
  height: 400px;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.review__sliderItemContent {
  padding: 32px;
  border-radius: 16px;
  background-color: #fff;
  box-shadow: 0 9px 32px 0 rgba(89, 92, 219, 0.05);
  margin: -144px 64px 0;
  position: relative;
  z-index: 2;
}

.review__sliderItemTitle {
  color: #1B1C57;
  font-size: 20px;
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 16px;
}

.review__sliderItemtext {
  margin-bottom: 24px;
}

.review__sliderItemPerson {
  display: flex;
  align-items: center;
}

.review__sliderItemPersonPhoto {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  box-shadow: 0 9px 32px 0 rgba(89, 92, 219, 0.1);
  margin-right: 24px;
  overflow: hidden;
}

.review__sliderItemPersonInfo {
  margin-right: 24px;
}

.review__sliderItemPersonName {
  color: #0E1735;
  font-size: 14px;
  font-weight: 400;
  line-height: lh(14,22);
  margin-bottom: 4px;
}

.review__sliderItemPersonProf {
  color: #888B97;
  font-size: 14px;
  font-weight: 400;
  line-height: lh(14,22);
}

.review__sliderItemPersonRating {
  display: flex;
  align-items: center;
  margin-left: auto;

  svg {
    fill: #F59E0B;
    height: 28px;
    width: 28px;
    margin-right: 16px;
  }

  span {
    color: #3C4563;
    font-size: 20px;
    font-weight: 500;
    line-height: lh(20,28);
  }
}


@mixin review-992 {

  .review__slider {
    width: 100%;
  }

}


@mixin review-768 {

  .review__titleWrapper {
    margin-bottom: 30px;
  }

  .review__slider {
    left: auto;
    transform: none;
  }

  .review__sliderItem {
    padding-bottom: calc( var(--indDesc) * 2.05 );
  }

  .review__sliderItemImg {
    max-width: 740px;
    height: calc( var(--indDesc) * 25.72 );
    width: 100%;
  }

  .review__sliderItemContent {
    margin-top: calc( var(--indDesc) * 9.26 * (-1) );
    margin-right: auto;
    margin-left: auto;
    width: 90%;
    padding: calc( var(--indDesc) * 2.05 );
  }

  .review__sliderItemTitle {
    font-size: 18px;
  }

}


@mixin review-576 {

  .review__sliderItemTitle {
    font-size: 16px;
    margin-bottom: calc( var(--indDesc) * 1.17 );
  }

  .review__sliderItemtext {
    margin-bottom: calc( var(--indDesc) * 1.76 );
  }

  .review__sliderItemPersonPhoto {
    margin-right: 15px;
  }

  .review__sliderItemPersonRating {

    svg {
      height: 20px;
      width: 20px;
      margin-right: calc( var(--indDesc) * 1.17 );
    }

    span {
      font-size: 16px;
    }
  }

}



